<template>
  <div 
    :class="[
      '__Top_Header_Box',
      `${ IsHideImgCheckChild ? '__Show' : '' }`
    ]"
  >
    <div class="__LR_Box _L">
      <div class="__Circular">
        <i class="__Day_T">{{ _Day }}</i>
      </div>
    </div>
    <div class="__Logo_Box">
      <img width="34" src="https://pic.imgdb.cn/item/63a941ed08b683016392b470.png">
    </div>
    <div class="__LR_Box _R">
      <Check 
        ref="HeaderCheckBtnRef"
        :class="[
          '__Circular',
          `${ IsHideImgCheck ? '' : IsHideImgCheckChild ? '__Open' : '' }`
        ]"
        @click="OpenImgCheck"
      ></Check>
    </div>
  </div>
</template>

<script setup>
  /**
   * icons
   */
  import Check from './icons/Check.vue';

  import { ref } from 'vue';

  /**
   * Pinia 数据层
   */
  import { appStore } from '@/store';
  import { storeToRefs } from 'pinia';
  const store = appStore();
  const { IsDisableFn } = storeToRefs(store);
  const { IsHideImgCheck } = storeToRefs(store);
  const { IsHideImgCheckChild } = storeToRefs(store);

  /**
   * 获取今日日期
   */
  let _Time = new Date();
  const _Day = ref(_Time.getDate());

  /**
   * 点击打开或关闭图片查看层
   */
  const OpenImgCheck = () => {
    /* 函数防抖 */
    if ( IsDisableFn.value ) return;
    IsDisableFn.value = true;
    setTimeout(() => IsDisableFn.value = false, 600);

    /* 控制属性值 */
    store.OpenOrCloseImgCheck();
  }
</script>

<style lang="less">
  .__Top_Header_Box {
    z-index: 9900;
    position: absolute;
    top: 36px;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 0px;
    height: 40px;
    background: #FFFFFF;
    border-radius: 36px;
    overflow: hidden;
    opacity: 0;
    transition: all 300ms ease-in-out;
    animation: __Header_Box 1200ms ease-in-out 1 400ms forwards;

    &.__Show {
      opacity: 1;
    }

    @keyframes __Header_Box {
      0% { width: 0px; }
      100% { width: 210px; }
    }

    .__Logo_Box {
      padding-top: 2px;
    }

    .__LR_Box {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 4px;
      width: 40px;
      height: 40px;

      &._L {
        .__Circular {
          width: 27px;
          height: 18px;
          border-radius: 10px;
          background-color: #000000;

          .__Day_T {
            display: inline-block;
            width: 100%;
            color: #FFFFFF;
            font-weight: 100;
            text-align: center;
          }
        }
      }
      
      &._R {
        .__Circular {
          width: 24px;
          height: 16px;
          fill: #4E4E4E;
          transition: fill 300ms ease-in-out;
          cursor: url('../assets/index_手掌.ico'), auto;

          &:hover {
            fill: #000000;
          }

          &.__Open {
            fill: #FF0000;

            &:hover {
              fill: #c40101;
            }
          }
        }
      }
    }
  }
</style>