// Vue & Pinia
import { ref } from 'vue'
import { defineStore } from 'pinia'

export const appStore = defineStore('app', {

  state: () => {
    return {
      IsDisableFn: false,           // 是否禁用函数方法（防抖处理）
      IsHideHome: false,            // 是否隐藏主页
      IsHideDecorate: false,        // 是否隐藏装饰点
      IsHideImgCheck: true,         // 是否隐藏图片查看层
      IsHideImgCheckChild: true,    // 是否隐藏图片查看层二级弹窗
      ImgCheckChildId: 1,           // 图片查看层二级弹窗ID
      ImgCheckChildData: {
        bgImgUrl: 'https://pic.imgdb.cn/item/63aeb3b808b6830163622f0f.png',
        nameImgUrl: 'https://pic.imgdb.cn/item/63aea10408b68301634474fb.png',
        nameText: '斋藤飞鸟',
        lineColor: '#3A8CF0',
        textList: [
          '系列是四个壁纸系列中的主系列，时代发展主要是围绕着斋藤飞鸟壁纸系列不断前进，斋藤飞鸟系列是发展最早的系列，早期完成了一个世纪篇，结束了旧时代的风格。最终此系列也在新的一个世纪开启了新的时代篇。',
          '平行时代是新的开始，代表着新的风格就此诞生，自此后新风格不断更新，打破了传统的旧思维。虽然旧时代中风格较为统一，但超光时代却很好的在旧时代中脱颖而出，成为了一大亮点，所以在一周年纪中，传奇时代致敬了超光时代（青春）。',
          '时代会不断前行，或许会遇到挫折，但前进的步伐并不会就此停下，出发，就在眼前。次也，终成就！'
        ]
      },        // 图片查看层二级弹窗数据
    }
  },

  actions: {
    /**
     * 打开或关闭图片查看层
     */
    OpenOrCloseImgCheck() {
      this.IsHideHome = !this.IsHideHome;
      this.IsHideDecorate = !this.IsHideDecorate;
      this.IsHideImgCheck = !this.IsHideImgCheck;
    },
    /**
     * 打开图片查看层二级弹窗
     * @param {选中的二级数据} _Data 
     */

    /**
     * 打开图片查看层二级弹窗
     * @param {选中的二级ID} _Id 
     * @param {选中的二级数据} _Data 
     */
    OpenCheckChildLayer(_Id, _Data) {
      this.ImgCheckChildId = _Id;
      this.ImgCheckChildData = _Data;
      this.IsHideImgCheckChild = false;
      this.IsHideImgCheck = true;
    },

    /**
     * 关闭图片查看层二级弹窗
     */
    CloseCheckChildLayer() {
      this.IsHideImgCheckChild = true;
      this.IsHideImgCheck = false;
    }
  }

})
