<template>
  <div 
    :class="[
      '__Decorate_Container',
      `${ IsHideDecorate ? '' : '__Show' }`
    ]"
  >

    <i class="__MT">/WALLPAPER|COLLECTION|WORKS/&nbsp;/DESIGNER|CIYE/</i>

    <div class="__StarBox __L">
      <Star  
        class="__Stars" 
        v-for="(item, index) in 16"
        :key="index"
      >
      </Star>
    </div>

    <div class="__StarBox __R">
      <Star  
        class="__Stars" 
        v-for="(item, index) in 16"
        :key="index"
      >
      </Star>
    </div>
  </div>
</template>

<script setup>
  import Star from './icons/Star.vue';

  /**
   * Pinia 数据层
   */
  import { appStore } from '@/store';
  import { storeToRefs } from 'pinia';
  const store = appStore();
  const { IsHideDecorate } = storeToRefs(store);
</script>

<style lang="less" scoped>
@import url('../public/font.less');

  .__Decorate_Container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: 0;
    transition: all 600ms ease-in-out;

    &.__Show { opacity: 1; }

    .__MT {
      position: absolute;
      bottom: 36px;
      left: 50%;
      transform: translate(-50%) scale(0.76);
      font-size: 12px;
      color: #FFFFFFa8;
      font-weight: 100;
      letter-spacing: 32px;
    }

    .__StarBox {
      position: absolute;
      top: 0;
      padding: 40px 14px;
      display: flex;
      justify-content: center;
      align-content: space-between;
      flex-wrap: wrap;
      width: 12px;
      height: 100%;

      &.__L { left: 0; }
      &.__R { right: 0; }
      
      .__Stars {
        width: 18px;
        animation-name: __Stars;
        animation-duration: 2000ms;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;

        @keyframes __Stars {
          0% { opacity: 1; }
          50% { opacity: 0; }
          100% { opacity: 1; }
        }
      }
    }
  }

  @MAX: 16;
  .StarsAnimation(@i, @t) when (@i <= @MAX) {
    .__StarBox .__Stars:nth-of-type(@{i}) { animation-delay: ~'@{t}ms'; }
    .StarsAnimation(@i + 1, @t + 400)
  }

  .StarsAnimation(1, 1000);
</style>
