<template>
  <div 
    :class="[
      '__Img_Check_Child_Container',
      `${IsHideImgCheckChild ? '' : '__Show'}`
    ]"
  >
    <div class="__Container _L">
      <img v-if="ImgCheckChildId === 1" src="https://pic.imgdb.cn/item/63aeb3b808b6830163622f0f.png">
      <img v-else-if="ImgCheckChildId === 2" src="https://pic.imgdb.cn/item/63aeb3b808b6830163622f34.png">
      <img v-else-if="ImgCheckChildId === 3" src="https://pic.imgdb.cn/item/63aeb3b808b6830163622f6a.png">
      <img v-else-if="ImgCheckChildId === 4" src="https://pic.imgdb.cn/item/63aeb3b808b6830163622f84.png">
      <img v-else :src="ImgCheckChildData.bgImgUrl">
    </div>

    <div class="__Container _R">
      <div 
        class="__Close_Btn_Box"
        @click="store.CloseCheckChildLayer()"
      >
        <CloseBtn class="__Icons"></CloseBtn>
      </div>

      <div class="__Text_Container">
        <div 
          :class="[
            '__Name_Text_Box',
            `${ ImgCheckChildData.nameText.length % 2 !== 0 ? '_Three_Length' : '' }`
          ]"
        >
          <div
            class="__Line_Down"
            :style="{ 'background-color': ImgCheckChildData.lineColor }"
          >
          </div>
          <span class="__Text">{{ ImgCheckChildData.nameText }}</span>
        </div>
        <span
          class="__Paragraph"
          v-for="(item, index) in ImgCheckChildData.textList"
          :key="index"
        >
          {{ item }}
        </span>
      </div>

      <div class="__Decorate_Box">
        <div class="__Dashed_Line _L"></div>
        <div class="__NameImg_Box">
          <img :src="ImgCheckChildData.nameImgUrl">
        </div>
        <div class="__Dashed_Line _R"></div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import CloseBtn from './icons/Close.vue'

  /**
   * Pinia 数据层
   */
  import { appStore } from '@/store';
  import { storeToRefs } from 'pinia';
  const store = appStore();
  const { IsHideImgCheckChild } = storeToRefs(store);
  const { ImgCheckChildId } = storeToRefs(store);
  const { ImgCheckChildData } = storeToRefs(store);
</script>

<style lang="less" scoped>
  .__Img_Check_Child_Container {
    z-index: 9999;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(28, 28, 28, 0.95);
    opacity: 0;
    pointer-events: none;
    transition: all 300ms ease-in-out;

    .__Container {
      float: left;
      position: relative;
      height: 100vh;
      overflow: hidden;

      &._L {
        width: 576px;
        
        img { 
          transform: translate(-100%);
          width: 100%; 
        }
      }

      &._R {
        width: 1344px;

        .__Close_Btn_Box {
          position: absolute;
          top: 35px;
          right: 35px;
          padding: 10px;
          width: 25px;
          height: 25px;
          cursor: url('../assets/index_手掌.ico'), auto;

          .__Icons {
            width: 100%;
            height: 100%;
            fill: #FFFFFF;
            transition: all 200ms ease-in-out;
          }

          &:hover {
            .__Icons {
              fill: #8a8a8a;
            }
          }
        }

        .__Text_Container {
          position: absolute;
          top: 17%;
          left: 50%;
          transform: translate(-50%);
          width: 1116px;
          height: auto;
          color: #FFFFFF;
          opacity: 0;

          .__Name_Text_Box {
            position: absolute;
            top: 0;
            left: 0;
            height: 46px;
            text-align: center;

            &._Three_Length {
              margin-left: 22px;
            }

            .__Line_Down {
              position: absolute;
              bottom: 0;
              width: 100%;
              height: 8px;
            }

            .__Text {
              position: relative;
              display: inline-block;
              width: 100%;
              height: 100%;
              font-size: 36px;
              font-weight: 900;
              line-height: 1.5;
            }
          }

          .__Paragraph {
            position: relative;
            top: 18.6px;
            display: inline-block;
            font-size: 20px;
            font-weight: 100;
            letter-spacing: 2px;
            line-height: 1.8;
            text-indent: 44px;

            &:nth-of-type(1) {
              text-indent: 150px;
            }
          }
        }

        .__Decorate_Box {
          position: absolute;
          bottom: 15%;
          left: 50%;
          transform: translate(-50%);
          display: flex;
          align-items: center;
          justify-content: space-around;
          flex-wrap: wrap;
          width: 1116px;
          height: 54px;

          .__Dashed_Line {
            width: 0;
            height: 0;
            border-top: 1PX dashed rgba(255, 255, 255, 0.66);
          }

          .__NameImg_Box {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 0;
            height: 54px;
            overflow: hidden;

            img {
              width: 100px;
            }
          }
        }
      }
    }

    &.__Show { 
      opacity: 1; 
      pointer-events: all;

      .__Container {
        &._L {
          img {
            animation: Container_Img_Show 800ms ease-in-out 1 200ms forwards;

            @keyframes Container_Img_Show {
              0% { transform: translate(-100%); }
              100% { transform: translate(0%); }
            }
          }
        }

        &._R {
          .__Text_Container {
            animation: Text_Container_Show 600ms ease-in-out 1 200ms forwards;

            @keyframes Text_Container_Show {
              0% { opacity: 0; }
              100% { opacity: 1; }
            }
          }

          .__Decorate_Box {
            .__Dashed_Line {
              animation: Dashed_Line_Show 600ms ease-in-out 1 200ms forwards;

              @keyframes Dashed_Line_Show {
                0% { opacity: 0; width: 0; }
                100% { opacity: 1; width: 430px; }
              }
            }

            .__NameImg_Box {
              animation: NameImg_Box_Show 600ms ease-in-out 1 200ms forwards;

              @keyframes NameImg_Box_Show {
                0% { opacity: 0; width: 0; }
                100% { opacity: 1; width: 100px; }
              }
            }
          }
        }
      }
    }
  }
</style>
